<template>
  <div class="container mt-2">
    <br>
    <div v-if="saveChange" class="alert alert-success text-center">
      <b style="font-size: 12px">İşlemler Kayıt Edildi</b>
    </div>
    <div class="d-flex justify-content-around">
      <button class="btn btn-secondary btn-sm" @click="newItem">Ürün ekle</button>
      <button class="btn btn-success btn-sm" @click="storeItemSaveChange">İşlemleri
        Kaydet
      </button>
    </div>
    <form-item v-for="(item,index) in items" :key="item"
               :index="index"
               :item="item"
               @add-form-item="addItem"
               @delete-form-item="deleteFormItem">
    </form-item>
  </div>
</template>

<script>
import FormItem from '@/components/FormItem.vue'

export default {
  components: {
    FormItem
  },
  data () {
    return {
      saveChange: false,
      items: []
    }
  },
  methods: {
    storeItemSaveChange () {
      this.$store.commit('setItems', this.items)
      console.log(this.$store.getters.items)
      this.localStorageSaveChange()
      this.saveChange = true
      setTimeout(() => {
        this.saveChange = false
      }, 2000)
    },
    localStorageSaveChange () {
      localStorage.setItem('adi', 'Ferhat')
      localStorage.setItem('items', JSON.stringify(this.items))
      console.log('Items are saved to local storage')
    },
    addItem (itemPayload) {
      this.items.push(itemPayload)
    },
    newItem () {
      this.items.push({
        barcode: null,
        quantity: null,
        salePrice: null,
        listPrice: null,
        autoStart: false
      })
    },
    deleteFormItem (item) {
      const itemIndex = this.items.findIndex(i => i.barcode === item.barcode)
      this.items.splice(itemIndex, 1)
    }
  },

  mounted () {
    // localde veri varsa yükle
    if (localStorage.getItem('items')) {
      this.items = JSON.parse(localStorage.getItem('items'))
    }
  }
}

</script>
