      <div class="col-4 mb-2">
        <input class="form-control" type="text" v-model="localItem.barcode" placeholder="Barkod">
      </div>
      <div class="col-4 mb-2">
        <input class="form-control" type="number" v-model.number="localItem.quantity" placeholder="Stok">
      </div>
      <div class="col-4 mb-2">
        <input class="form-control" type="number" v-model.number="localItem.salePrice"
